		import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
	
	export default {
		components:{
			uniNumberBox,
		},
		data() {
			return {
				sumPrice: '0.00',
				headerPosition: "fixed",
				headerTop: null,
				statusTop: null,
				selectedList: [],
				isAllselected: false,
				goodsList: [],
				//控制滑动效果
				theIndex: null,
				oldIndex: null,
				isStop: false
			}
		},
		// onPageScroll(e) {
		// 	//兼容iOS端下拉时顶部漂移
		// 	this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
		// 	this.headerTop = e.scrollTop >= 0 ? null : 0;
		// 	this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
		// },
		//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
		onPullDownRefresh() {
			setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 1000);
		},
		onLoad() {
			
			//兼容H5下结算条位置






		},
			//分享页面 
		onShareAppMessage: function() {
			var that = this;
			return {
				title: '快来看一看啊！',
				path: '/pages/tabBar/cart',
			}
		},
		methods: {
			
		
			//加入商品 参数 goods:商品数据
			joinGoods(goods) {
				/*
				 * 这里只是展示一种添加逻辑，模板并没有做从其他页面加入商品到购物车的具体动作，
				 * 在实际应用上，前端并不会直接插入记录到goodsList这一个动作，一般是更新列表和本地列表缓存。
				 * 一般商城购物车的增删改动作是由后端来完成的,
				 * 后端记录后返回前端更新前端缓存
				 */
				let len = this.goodsList.length;
				let isFind = false; //是否找到ID一样的商品
				for (let i = 0; i < len; i++) {
					let row = this.goodsList[i];
					if (row.id == goods.id) { //找到商品一样的商品
						this.goodsList[i].number++; //数量自增
						isFind = true; //找到一样的商品
						break; //跳出循环
					}
				}
				if (!isFind) {
					//没有找到一样的商品，新增一行到购物车商品列表头部
					this.goodsList[i].unshift(goods);
				}
			},
			//控制左滑删除效果-begin
			touchStart(index, event) {
				console.log(index,event,"start")
				//多点触控不触发
				if (event.touches.length > 1) {
					this.isStop = true;
					return;
				}
				this.oldIndex = this.theIndex;
				this.theIndex = null;
				//初始坐标
				this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
			},
			touchMove(index, event) {
				console.log(index,event,"move")
				//多点触控不触发
				if (event.touches.length > 1) {
					this.isStop = true;
					return;
				}
				let moveX = event.touches[0].pageX - this.initXY[0];
				let moveY = event.touches[0].pageY - this.initXY[1];
                console.log(moveX,moveY)
				if (this.isStop || Math.abs(moveX) < 5) {
					return;
				}
				if (Math.abs(moveY) > Math.abs(moveX)) {
					// 竖向滑动-不触发左滑效果
					this.isStop = true;
					return;
				}

				if (moveX < 0) {
					console.log("movex<0")
					this.theIndex = index;
					this.isStop = true;
				} else if (moveX > 0) {
					console.log("movex>0")
					if (this.theIndex != null && this.oldIndex == this.theIndex) {
						this.oldIndex = index;
						this.theIndex = null;
						this.isStop = true;
						setTimeout(() => {
							this.oldIndex = null;
						}, 150)
					}
				}
			},
			touchEnd(index, event) {
				console.log(index,event,"end")
				//结束禁止触发效果
				this.isStop = false;
			},
			//控制左滑删除效果-end


			//商品跳转
			toGoods(e) {
				uni.showToast({
					title: '商品' + e.goods_id,
					icon: "none"
				});
				uni.navigateTo({
					url: '../goods/goods?id='+e.goods_id,
				});
			},
			//跳转确认订单页面
			toConfirmation() {
			   //  console.log(this.goodsList)
				 if(this.goodsList.length>0){
					  let data={};
					 let arr=[]
					 this.goodsList.forEach(item=>{
					 					 if(item.selected){
					 						 let obj={
					 							 goods_id:item.goods_id,
					 							 goods_number:item.number,
					 							 goods_spec:item.goods_spec,
					 						 }
					 						 arr.push(obj);
					 					 }
					 })
					// console.log("data",data);
					 data.isCart = 1;
					 data.goods = arr;
					 uni.setStorage({
					 	key:"cart",
					 	data:data,
					 	success: () => {
					 		
					 	}
					 })
					 uni.navigateTo({
					 	url:"/pages/order/confirmation?iscart=2"
					 })
				 }else{
					 uni.showToast({
					 	title:"还没有商品哟"
					 })
				 }
				
			},
			//删除商品
			deleteGoods(id) {
				let len = this.goodsList.length;
				for (let i = 0; i < len; i++) {
					if (id == this.goodsList[i].id) {
						this.goodsList.splice(i, 1);
						break;
					}
				}
				this.selectedList.splice(this.selectedList.indexOf(id), 1);
				this.sum();
				this.oldIndex = null;
				this.theIndex = null;
				// 删除购物车后台；
				this.delCart(id);
			},
			// 删除购物车
			delCart(id){
					this.request({
						method:"post",
						data:{
							token:this.token,
							id:id,
						},
						url:this.config.url+"member/del_car",
						success:function(res){
							// console.log("res",res);
						},
					})
				
			},
			// 删除商品
			deleteList() {
				let len = this.selectedList.length;
				// console.log(this.selectedList);
				while (this.selectedList.length > 0) {
					this.deleteGoods(this.selectedList[0]);
				}
				this.selectedList = [];
				this.isAllselected = this.selectedList.length == this.goodsList.length && this.goodsList.length > 0;
				this.sum();
			},
			// 选中商品
			selected(index) {
				this.goodsList[index].selected = this.goodsList[index].selected ? false : true;
				let i = this.selectedList.indexOf(this.goodsList[index].id);
				i > -1 ? this.selectedList.splice(i, 1) : this.selectedList.push(this.goodsList[index].id);
				this.isAllselected = this.selectedList.length == this.goodsList.length;
				if(this.selectedList.length==this.goodsList.length){
					this.isAllselected=true;
				}
				this.sum();
			},
			//全选
			allSelect() {
				let len = this.goodsList.length;
				let arr = [];
				for (let i = 0; i < len; i++) {
					this.goodsList[i].selected = this.isAllselected ? false : true;
					arr.push(this.goodsList[i].id);
				}
				this.selectedList = this.isAllselected ? [] : arr;
				this.isAllselected = this.isAllselected || this.goodsList.length == 0 ? false : true;
				// console.log("this.selectedList",this.selectedList);
				this.sum();
			},
			// 改变数量
			bindChange(index,row,val){
				// console.log("row.number",row.number);
				// console.log("val",val);
				//增加 
				if(val>row.number){
					this.add(index);
					this.changeNum(row.id,1);
				}
				//减少 
				if(val<row.number){
					this.sub(index);
					this.changeNum(row.id,2);
				}
			},
			// 改变数量后台
			changeNum(id,type){
				this.request({
					method:"post",
					data:{
						token:this.token,
						id:id,
						type:type
					},
					url:this.config.url+"member/resume_car",
					success:function(res){
						//console.log("res",res);
					},
				})
			},
			// 减少数量
			sub(index) {
				if (this.goodsList[index].number <= 1) {
					return;
				}
				this.goodsList[index].number--;
				this.sum();
			},
			// 增加数量
			add(index) {
				this.goodsList[index].number++;
				this.sum();
			},
			// 合计
			sum(e, index) {
				this.sumPrice = 0;
				let len = this.goodsList.length;
				// console.log("len",len);
				// console.log("e",e);
				// console.log("index",index);
				for (let i = 0; i < len; i++) {
					if (this.goodsList[i].selected) {
						if (e && i == index) {
							this.sumPrice = this.sumPrice + (e.detail.value * this.goodsList[i].price);
						} else {
							this.sumPrice = this.sumPrice + (this.goodsList[i].number * this.goodsList[i].price);
							// console.log("this.sumPrice",this.sumPrice);
						}
					}
				}
				this.sumPrice = this.sumPrice.toFixed(2);
				// console.log(this.sumPrice)
			},
			discard() {
				//丢弃
			}
			
        },
		onShow(){
			// 获取购物车信息
			this.isAllselected=false;
			let that=this;
			this.request({
				method:"post",
				data:{
					token:this.token
				},
				url:this.config.url+"member/car",
				success:function(res){
					// console.log("data",res.data);
					if(res.data.code==1){
						that.goodsList=res.data.data;
						that.goodsList.forEach(item=>{
							item.spec=item.goods_spec;
							item.img=item.logo;
							item.name=item.title;
							item.selected=false;
						})
						
					}else{
						uni.showToast({
							icon:'none',
							title:"请求失败"
						})
					}
					
				}
			})
		},
		mounted() {
			
		}
	}
